.alphabet-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  background-color: tan;
}

.alphabet-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.alphabet-letter {
  background-color: white;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  font-size: 20px;
  text-transform: uppercase;
}